.item {
  display: flex;
  flex-direction: column;

  cursor: pointer;

  transition: opacity .3s ease;

  &:hover {
    opacity: .8;
  }

  &__title {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px; /* 13.466px */

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    color: #292929;
  }

  &__info {
    display: flex;
    align-items: center;
    gap: 16px;

    .item {
      &__category {
        font-family: Manrope, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 15px; /* 10.1px */

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        color: #555353;
      }

      &__openTill {
        font-family: Manrope, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 84.164%; /* 10.1px */

        white-space: nowrap;

        color: #8A8A8A;
      }
    }
  }
}
